@keyframes heartbeat {
  from {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1.2);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(1.11);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(1.18);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(1.07);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1.2);
  }
}

.heartbeat {
